import React,{useState, useContext, useEffect} from 'react'
import '../../assets/styles/UserInfoBar.css'
import { UserContext } from '../../App';
function UserInfoBar({user}) {




  return (
    <div className='info-bar-container'>
      <div className='info-data'>
      <label htmlFor="number">ID lena</label>
    <b>{user.id}</b>
     <label htmlFor="number">Jmno</label>
     <b>{user.fName + " " + user.lName}</b>
     <label htmlFor="date">Datum narozen</label>
     <b>{user.date}</b>
     <label htmlFor="number">Adresa</label>
     <b>{user.adress}</b>
     <label htmlFor="number">Organizace</label>
     <b>{user.organization}</b>
     <label htmlFor="number">Znmka</label>
    <b>{user.stamp}</b>
     <label htmlFor="number">Brigdy</label>
     <b>{user.jobs}</b>
     <label htmlFor="number">Povolenka</label>
     <b>{user.permit}</b>
      </div>

    </div>
  )
}

export default UserInfoBar