import './App.css';
import React,{useState, createContext} from 'react';
import Header from './components/navBar/Header';
import Footer from './components/Footer';
import Home from './pages/Home';
import ListOfGrounds from './pages/ListOfGrounds'
import GroundInfo from './pages/GroundInfo'
import Page404 from './pages/Page404'
import Login from './pages/Login'
import { Route, Routes } from 'react-router-dom';
import FishingRules from './pages/FishingRules';
import PrivateRoute from './components/PrivateRoute';
export const UserContext = createContext();
export const EditItemContext = createContext();

function App() {
  const [user, setUser] = useState("");
  const [itemToEdit, setItemToEdit] = useState();
  return (
    <>
    <UserContext.Provider value={[user, setUser]}>
    <EditItemContext.Provider value={[itemToEdit, setItemToEdit]}>
    <Header />      
        
        <Routes>
         <Route path='/'   element={<Home/>} /> {/*Domovsk strnka*/}
         <Route path='/login' element={<Login/>} /> {/*Cesta na: Pihlen*/}
         <Route path='/seznam-reviru'  element={<ListOfGrounds/>} /> {/*Cesta na: Strnka seznam revr*/}
         <Route path='/rybarsky-rad'  element={<FishingRules/>} /> {/*Cesta na: Strnka rybsk d*/}
         <Route path='/revir/:number' element={<GroundInfo/>} /> {/*Dynamicka cesta: pro informace o konkrtnm revru*/}
         <Route path='/dashboard' element={<PrivateRoute pageToReturn= "dashboard" />}/> {/*Cesta na: strnka pehled uivatele*/}
         <Route path='/zaznam/:type' element={<PrivateRoute pageToReturn="zaznam" />}/> {/*Cesta na: strnka pehled uivatele*/}
         <Route path="*" element={<Page404 />} /> {/*Chybov strnka 404*/}
       </Routes>
       </EditItemContext.Provider>
       </UserContext.Provider>

       <Footer />

    </>
  );
}

export default App;
